
.search-wrap {
  margin-bottom: 1em;
  display: flex;
  align-items: center; }

.search-input {
  height: 2em;
  flex-grow: 1;
  font-size: 16px;
  margin-right: 1em;
  border: none;
  outline: none;
  border-bottom: 1px solid var(--customDark);
  background: transparent;
  -webkit-transition: border-color 0.3s ease-in;
  transition: border-color 0.3s ease-in;
  font-family: "Neucha", cursive; }
  .search-input:focus {
    border-bottom: 1px solid var(--customYellow); }

.search-button {
  cursor: pointer;
  border: 2px solid var(--customYellow);
  border-radius: 8px;
  background-color: var(--customDark);
  color: #fff;
  font-size: 16px;
  padding: 0.5em 1em;
  font-family: inherit;
  -webkit-transition: background-color 0.3s ease-in;
  transition: background-color 0.3s ease-in; }
  .search-button:hover {
    background-color: rgba(32, 35, 41, 0.75); }

.sorting-fieldset {
  border: none;
  margin: 0 0 2em;
  padding: 0; }

.sorting-legend {
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 1em; }

.sorting-label {
  display: flex;
  margin-bottom: 0.5em;
  font-size: 16px; }
  .sorting-label:last-child {
    margin-bottom: 0; }

/* radio buttons styling */
.filter-input {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

.sorting-label-text {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5em;
  height: 1.5em;
  margin-right: 1em;
  border: 1px solid var(--customYellow);
  border-radius: 2px;
  -webkit-transition: border-color 0.3s ease-in;
  transition: border-color 0.3s ease-in; }
  .sorting-label-text:before {
    content: "";
    width: 5px;
    height: 11px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    opacity: 0; }

.filter-input:checked + .sorting-label-text {
  background-color: var(--customYellow); }

.filter-input:checked + .sorting-label-text:before {
  opacity: 1; }

.filter-input:focus + .sorting-label-text {
  outline: -webkit-focus-ring-color auto 5px; }

.filter-input:-moz-focusring + .sorting-label-text {
  outline: 1px dotted; }

.reset-filter-button {
  cursor: pointer;
  border: 2px solid var(--customYellow);
  border-radius: 8px;
  background-color: var(--customDark);
  color: #fff;
  font-size: 16px;
  padding: 0.5em 1em;
  font-family: inherit;
  -webkit-transition: background-color 0.3s ease-in;
  transition: background-color 0.3s ease-in; }
  .reset-filter-button:hover {
    background-color: rgba(32, 35, 41, 0.75); }

.loader-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  background-color: var(--main-bg); }

.loader {
  display: inline-block;
  width: 64px;
  height: 64px; }
  .loader:after {
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid var(--customYellow);
    border-color: var(--customYellow) transparent var(--customYellow) transparent;
    -webkit-animation: loader 1.2s linear infinite;
            animation: loader 1.2s linear infinite; }

@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 2em 0; }
  .pagination .page-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    color: var(--customDark);
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    border: 2px solid transparent; }
    .pagination .page-link:hover {
      color: #fff;
      background-color: var(--customDark); }
  .pagination .page-item {
    margin-right: 0.5rem; }
    .pagination .page-item:last-child {
      margin-right: 0; }
    .pagination .page-item.active .page-link {
      background-color: var(--customDark);
      border: 2px solid var(--customYellow);
      color: #fff; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }

.header {
  text-align: center;
  padding: 1em 0;
  background: var(--customDark); }

.main-title {
  color: var(--customYellow);
  margin-bottom: 1rem; }

.main-navigation-list {
  display: flex;
  justify-content: center; }

.main-navigation-list__item {
  margin-right: 1em;
  font-size: 1.25em; }
  .main-navigation-list__item:last-child {
    margin-right: 0; }
  .main-navigation-list__item a {
    position: relative;
    color: #fff !important; }
    .main-navigation-list__item a:after {
      content: "";
      bottom: -5px;
      display: block;
      width: 100%;
      height: 2px;
      background-color: var(--customYellow);
      opacity: 0;
      -webkit-transition: opacity 0.3s ease-in;
      transition: opacity 0.3s ease-in; }
  .main-navigation-list__item a.active::after {
    opacity: 1; }

/* my color palette variables */
:root {
  --text-color: #222f3e;
  --section-color: #54a0ff;
  --border-color: #c8d6e5;
  --custom-red: #ff6b6b;
  --main-bg: #dfe4ea;
  --base-color: #ced4da;
  --light-background: lighten(desaturate($base-color, 50%), 12.5%);
  --customYellow: rgb(255, 152, 0);
  --customDark: rgb(32, 35, 41); }

body {
  margin: 0;
  padding: 0;
  font-family: "Neucha", cursive !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

/* basic reset */
* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

html {
  font-size: 14px; }

body {
  font-weight: normal;
  font-style: normal;
  color: #222f3e;
  color: var(--text-color);
  min-height: 100vh;
  background-color: #dfe4ea;
  background-color: var(--main-bg); }

ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0; }

section {
  font-size: 1rem; }

a {
  color: #222f3e;
  color: var(--text-color);
  text-decoration: none; }

a:active {
  text-decoration: none; }

a:active,
a:focus,
a:visited {
  color: #222f3e;
  color: var(--text-color); }

img {
  max-width: 100%;
  height: auto; }

p {
  line-height: 1.5; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }

.row {
  display: flex; }

.filter-panel {
  max-width: 25%;
  flex-grow: 1; }

.characters-panel {
  max-width: 75%;
  flex-grow: 3; }

.main-content,
.page-section {
  padding-top: 1.5em; }

.section-title {
  font-size: 2rem;
  margin-bottom: 1.5rem; }

